@font-face {
  font-family: "Galien-Bold";
  src: url("../fonts/Galien-Bold.woff2") format("woff2"), url("../fonts/Galien-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gilroy-Regular";
  src: url("../fonts/Gilroy-Regular.woff2") format("woff2"), url("../fonts/Gilroy-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gilroy-Semibold";
  src: url("../fonts/Gilroy-Semibold.woff2") format("woff2"), url("../fonts/Gilroy-Semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
* {
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  max-width: 100%;
  height: auto;
}
body {
  min-width: 320px;
  font-family: "Gilroy-Regular", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.05em;
  margin: 0;
  padding: 0;
  color: #1F3F68;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.font-header {
  font-family: "Galien-Bold", "Arial", sans-serif;
  font-weight: 700;
}
.font-regular {
  font-family: "Gilroy-Regular", "Arial", sans-serif;
  font-weight: 400;
}
.font-bold {
  font-family: "Gilroy-Semibold", "Arial", sans-serif;
  font-weight: 600;
}
.button {
  display: block;
  width: 220px;
  padding: 15px 0;
  text-align: center;
  vertical-align: middle;
  font-family: "Gilroy-Regular", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.04em;
  color: #FFFFFF;
  background: #5A98D0;
  box-shadow: 5px 10px 20px rgba(53, 110, 173, 0.2);
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
.button:hover {
  opacity: 0.6;
}
.button:active {
  opacity: 0.3;
}
.header {
  width: 100%;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1366px;
  margin: 0 auto;
  padding: 48px 85px 0;
}
.header__logo {
  line-height: 0;
  font-size: 0;
}
.header__logo:hover {
  opacity: 0.6;
}
.header__logo:active {
  opacity: 0.3;
}
@media (max-width: 768px) {
  .header__wrapper {
    padding: 48px 30px 0;
  }
}
.navigation__toggle {
  padding: 0;
  background-color: transparent;
  border: none;
  line-height: 0;
  font-size: 0;
}
.navigation--closed svg {
  margin: 8px 0 0 0;
  cursor: pointer;
}
.navigation--closed svg:hover {
  opacity: 0.6;
}
.navigation--closed svg:active {
  opacity: 0.3;
}
.intro {
  width: 100%;
  background: linear-gradient(0deg, rgba(152, 195, 232, 0.2) 8.84%, rgba(152, 195, 232, 0) 31.12%);
}
.intro__wrapper {
  max-width: 1366px;
  min-height: 688px;
  margin: 0 auto;
  padding: 0 212px;
  background: url('../img/illustration.svg') bottom 55px right 22px no-repeat;
}
.intro__header {
  max-width: 515px;
  margin: 0;
  padding: 98px 0 0 0;
  font-family: "Galien-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 64px;
  line-height: 74px;
  letter-spacing: 0.04em;
  color: #1F3F68;
}
.intro__text {
  max-width: 425px;
  margin: 0;
  padding: 42px 0 0 0;
}
.intro__button {
  margin: 78px 0 0 0;
}
@media (max-width: 1200px) {
  .intro__wrapper {
    padding: 0 85px;
  }
}
@media (max-width: 1000px) {
  .intro__wrapper {
    background-size: 480px;
  }
}
@media (max-width: 768px) {
  .intro__wrapper {
    padding: 0 30px;
    background: none;
  }
  .intro__header {
    margin: 0 auto;
    text-align: center;
    font-size: 54px;
    line-height: 64px;
  }
  .intro__text {
    margin: 0 auto;
    text-align: center;
  }
  .intro__button {
    margin: 78px auto 50px;
  }
}
.services {
  width: 100%;
}
.services__wrapper {
  position: relative;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 212px;
}
.services__header {
  position: absolute;
  max-width: 360px;
  margin: 0 0 0 7px;
  padding: 192px 0 0 0;
  font-size: 36px;
  line-height: 46px;
  letter-spacing: 0.05em;
  color: #316099;
}
.services__list {
  margin: 0;
  padding: 168px 0 137px 0;
  list-style: none;
  columns: 2;
}
.services__item {
  display: inline-block;
  width: 100%;
  min-height: 533px;
  padding: 25px 0 0 0;
  margin: 0 0 75px 0;
}
.services__item:first-child {
  min-height: 215px;
  visibility: hidden;
}
.services__item-wrapper {
  position: relative;
  max-width: 380px;
  min-height: 525px;
  margin-left: auto;
  padding: 0 48px;
  background: #FFFFFF;
  box-shadow: 5px 20px 50px rgba(16, 112, 177, 0.2);
  border-radius: 10px;
}
.services__item-image {
  display: block;
  position: absolute;
}
.services__item:nth-child(2) .services__item-image {
  top: 45px;
  left: 65px;
}
.services__item:nth-child(3) .services__item-image {
  top: -23px;
  left: 67px;
}
.services__item:last-child .services__item-image {
  top: 48px;
  left: 85px;
}
.services__item-header {
  margin: 0;
  padding: 233px 0 0 0;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.05em;
  color: #1F3F68;
}
.services__item-text {
  margin: 0;
  padding: 15px 0 0 0;
}
.services__item-link {
  position: relative;
  display: block;
  width: 124px;
  margin: 30px 0 0 0;
  padding: 5px 0;
  font-family: "Gilroy-Semibold", "Arial", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 30px;
  letter-spacing: 0.05em;
  color: #5A98D0;
}
.services__item-link:hover {
  opacity: 0.6;
}
.services__item-link:active {
  opacity: 0.3;
}
.services__item-link svg {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
@media (max-width: 1200px) {
  .services__wrapper {
    padding: 0 85px;
  }
}
@media (max-width: 1000px) {
  .services__header {
    position: relative;
    margin: 0 auto;
    padding: 98px 0 50px 0;
    text-align: center;
  }
  .services__list {
    padding: 0 0 50px 0;
    columns: 1;
  }
  .services__item-wrapper {
    margin: 0 auto;
  }
  .services__item {
    margin: 0 0 15px 0;
  }
  .services__item:first-child {
    min-height: 0;
    margin: 0;
    padding: 0;
  }
}
@media (max-width: 768px) {
  .services__wrapper {
    padding: 0 30px;
  }
}
@media (max-width: 440px) {
  .services__item-image {
    width: 60%;
  }
  .services__item:nth-child(2) .services__item-image {
    top: 45px;
    left: 50%;
    transform: translateX(-50%);
  }
  .services__item:nth-child(3) .services__item-image {
    top: 45px;
    left: 50%;
    transform: translateX(-50%);
  }
  .services__item:last-child .services__item-image {
    top: 45px;
    left: 50%;
    transform: translateX(-50%);
  }
}
.clients {
  width: 100%;
  background: linear-gradient(90deg, transparent 50%, #5A98D0 50%);
}
.clients__wrapper {
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 212px;
  background: #5A98D0;
  border-radius: 200px 0px 0px 0px;
}
.clients__header-wrapper {
  display: flex;
  padding: 95px 0 0 0;
}
.clients__header {
  width: 230px;
  min-width: 230px;
  margin: 0 90px 0 0;
  font-size: 36px;
  line-height: 46px;
  letter-spacing: 0.05em;
  color: #FFFFFF;
}
.clients__text {
  max-width: 540px;
  margin: 0;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.05em;
  color: #FFFFFF;
  opacity: 0.6;
}
.clients__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 110px 0 126px;
}
.clients__item {
  width: 25%;
  padding: 41px 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 46px;
  letter-spacing: 0.02em;
  color: #F2F2F2;
}
.clients__item:last-child {
  text-transform: capitalize;
  background: rgba(31, 63, 104, 0.2);
  cursor: pointer;
}
.clients__item:last-child:hover {
  background: rgba(31, 63, 104, 0.3);
}
.clients__item:last-child:active {
  opacity: 0.6;
}
.clients__item:not(:nth-child(4n+4)) {
  border-right: 1px solid #FFFFFF;
}
.clients__item:nth-child(-n+4) {
  border-bottom: 1px solid #FFFFFF;
}
@media (max-width: 1200px) {
  .clients__wrapper {
    padding: 0 85px;
  }
}
@media (max-width: 768px) {
  .clients__wrapper {
    padding: 0 30px;
    border-radius: 0;
  }
  .clients__header-wrapper {
    display: flex;
    flex-direction: column;
  }
  .clients__header {
    width: auto;
    min-width: auto;
    padding: 0 0 20px 0;
    margin: 0 auto;
    text-align: center;
  }
  .clients__text {
    max-width: auto;
    margin: 0 auto;
    text-align: center;
  }
  .clients__list {
    padding: 30px 0 50px;
  }
  .clients__item {
    width: 50%;
    font-size: 16px;
    line-height: 20px;
  }
  .clients__item:nth-child(2n) {
    border-right: none;
  }
  .clients__item:nth-child(-n+6) {
    border-bottom: 1px solid #FFFFFF;
  }
}
.action {
  width: 100%;
  background: linear-gradient(90deg, #316099 50%, #5A98D0 50%);
}
.action__wrapper {
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 96px 0 212px;
}
.action__inner-wrapper {
  display: flex;
  justify-content: space-between;
  background: #316099;
  border-radius: 0px 200px 0px 0px;
  padding: 90px 196px 100px 0;
}
.action__header-wrapper {
  width: 460px;
}
.action__header {
  width: 320px;
  margin: 0;
  padding: 0;
  font-size: 36px;
  line-height: 46px;
  letter-spacing: 0.05em;
  color: #FFFFFF;
}
.action__text {
  margin: 0;
  padding: 32px 0 0 0;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.05em;
  color: #FFFFFF;
  opacity: 0.6;
}
.action__button {
  margin: auto 0;
}
@media (max-width: 1200px) {
  .action__wrapper {
    padding: 0 85px;
  }
  .action__inner-wrapper {
    flex-direction: column;
  }
  .action__header-wrapper {
    width: auto;
  }
