@media (max-width: 768px) {
  .action__wrapper {
    padding: 0 30px;
    background: #316099;
  }
  .action__inner-wrapper {
    padding: 90px 0 0;
  }
  .action__header {
    width: auto;
    max-width: 320px;
    margin: 0 auto;
    text-align: center;
  }
  .action__text {
    text-align: center;
  }
  .action__button {
    margin: auto;
  }
}
.footer {
  width: 100%;
  background: linear-gradient(90deg, #316099 50%, #5A98D0 50%);
