}
.footer__inner-wrapper {
  background: #316099;
  padding: 0 196px 0 0;
}
.footer__top-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 95px 0 60px;
  border-top: 1px solid rgba(255, 255, 255, 0.4);
}
.footer__top-wrapper a:hover {
  opacity: 0.6;
}
.footer__top-wrapper a:active {
  opacity: 0.3;
}
